const sortOptions = [
  {
    value: 'featured',
    label: 'Featured'
  },
  {
    value: 'lowest',
    label: 'Lowest'
  },
  {
    value: 'highest',
    label: 'Highest'
  }
]

function ProgrammaticallyDropzone(props) {
  const [files, setFiles] = useState([])
  const { getRootProps, getInputProps, open } = useDropzone({
    //accept: "image/*",
    noClick: true,
    noKeyboard: true,
    onDrop: (acceptedFiles) => {
      props.setfilesOndrop(acceptedFiles)
      setFiles(
        acceptedFiles.map((file) =>
          Object.assign(file, {
            preview: URL.createObjectURL(file)
          })
        )
      )
    }
  })

  const thumbs = files.map((file) => (
    // <div style={{display: 'flex', flexDirection: 'column'}}>
    //   <div>
    //   <span> {file.name}</span>
    //   </div>
    <div className="dz-thumb" key={file.name}>
      <div className="dz-thumb-inner">
        <img
          src={file.preview}
          className="dz-img"
          onError={(e) => {
            e.target.src = img5
          }}
          alt={file.name}
        />
        <br />
        <span> {file.name}</span>
      </div>
    </div>
    // </div>
  ))

  useEffect(
    () => () => {
      // Make sure to revoke the data uris to avoid memory leaks
      files.forEach((file) => URL.revokeObjectURL(file.preview))
    },
    [files]
  )

  useEffect(() => {
    if (props.resetDropzone) {
      setFiles([])
    }
  }, [props.resetDropzone])

  return (
    <section>
      <div {...getRootProps({ className: 'dropzone' })} style={{minHeight: '48px'}}>
        <input {...getInputProps()} />
        <button className="mx-1" onClick={open}>
          Upload
        </button>
      </div>
      {/* <br />
      <Button.Ripple
        color="warning"
        outline
        className="mr-1 mb-1 btn-block"
        size="lg"
        block
        onClick={open}
      >
        Select File
      </Button.Ripple> */}
      {thumbs.length ? (
        <aside className="thumb-container-documents">{thumbs}</aside>
      ) : (
        <div {...getRootProps({ className: 'dropzone-dummy' })}>
          <p className="mx-1">Preview Selected files</p>
        </div>
      )}
    </section>
  )
}

class ShopContent extends React.Component {
  state = {
    inCart: [],
    inWishlist: [],
    view: 'grid-view',
    data: []
  }

  componentWillMount = () => {
   
    axios.get('/paragon/api/products/list').then((res) => {
      console.log('ressssss', res.data);
      this.setState({data: res.data})
    })
  }
  handleAddToCart = (i,id) => {
    let cartArr = this.state.inCart
    cartArr.push(i)
    this.setState({
      inCart: cartArr
    })
    let user =  JSON.parse(localStorage.getItem('logInUserData'))
    const idx =user.cart.findIndex(i => i.productId === id);
    if(idx >= 0){
      user.cart[idx].qty = user.cart[idx].qty+1; 
    } else {
      user.cart.push({productId:id,qty:1});
    }
    

    const postBody= {
      _id: user._id,
      cart: user.cart
    }
    axios.post('/paragon/api//update/user',postBody).then((res) => {
     // console.log('ressssss', res.data);
     // this.setState({data: res.data})
    })
    localStorage.setItem('logInUserData',JSON.stringify(user));
  }

  movetowishlist = (item) => {
    let user =  JSON.parse(localStorage.getItem('logInUserData'))
  
    user.wishlist.push(item.productId);
  
    const postBody= {
      _id: user._id,
     
      wishlist: user.wishlist
    }
    axios.post('/paragon/api//update/user',postBody).then((res) => {
     // console.log('ressssss', res.data);
     // this.setState({data: res.data})
    
    
    })
    
    localStorage.setItem('logInUserData',JSON.stringify(user));
    //window.location.reload();

  }
  handleView = (view) => {
    this.setState({
      view
    })
  }

  setfilesOndrop = (data) => {
    this.setState({ reset: false })
    this.setState({
      files: data.map((file) =>
        Object.assign(file, {
          preview: URL.createObjectURL(file)
        })
      )
    })
  }

  resetDropzone = () => {
    this.setState({ reset: true })
  }
  onError=(e)=> {
    console.log(e, 'error in file-viewer');
  }
  

  handleWishlist = (i) => {
    let wishlistArr = this.state.inWishlist
    if (!wishlistArr.includes(i)) wishlistArr.push(i)
    else wishlistArr.splice(wishlistArr.indexOf(i), 1)
    this.setState({
      inWishlist: wishlistArr
    })
  }

  render() {
    let renderProducts = this.state.data.map((product, i) => {
      return (
        <Card className="ecommerce-card" key={i}>
          <div className="card-content">
            <div className="item-img text-center">
              <Link to={"/ecommerce/product-detail/"+product.productId}>
                <img
                  className="img-fluid"
                  src={product.image && product.image != '' ? product.image : 'https://i.pinimg.com/736x/da/1f/9e/da1f9e1050f1cc2cccc390a8c1b5aa40.jpg'}
                  alt={product.name}
                />
              </Link>
            </div>
            <CardBody>
              <div className="item-wrapper">
                <div className="item-rating">
                  <Badge color="success" className="badge-md">
                    <span className="mr-50 align-middle">4</span>
                    <Star size={14} />
                  </Badge>
                </div>
                <div className="product-price">
                  <p>MRP ₹<strike>{product.mrp}</strike> <span style={{color:'green'}}> &nbsp;&nbsp;
                  {Math.round(Number(100 - ((Number(product.price) /Number(product.mrp))*100)))}% off</span></p>
                  <h6 className="item-price" style={{float:'right'}}> ₹{product.price}</h6>
                </div>
              </div>
              <div className="item-name">
                <Link to="/ecommerce/product-detail">
                  {' '}
                  <span>{product.name}</span>
                </Link>
                <p className="item-company">
                  By <span className="company-name">{product.by}</span>
                </p>
              </div>
              <div className="item-desc">
                <p className="item-description">{product.desc}</p>
              </div>
            </CardBody>
            <div className="item-options text-center">
              <div className="item-wrapper">
                <div className="item-rating">
                  <Badge color="primary" className="badge-md">
                    <span className="mr-50 align-middle">4</span>
                    <Star size={14} />
                  </Badge>
                </div>
                <div className="product-price">
                  <h6 className="item-price">MRP ₹<strike>{product.mrp}</strike> <p style={{color:'lightgreen'}}>5% off</p> {product.price}</h6>
                </div>
              </div>
              <div className="wishlist" onClick={() => {this.handleWishlist(i); this.movetowishlist(product);}}>
                <Heart
                  size={15}
                  fill={
                    this.state.inWishlist.includes(i)
                      ? '#EA5455'
                      : 'transparent'
                  }
                  stroke={
                    this.state.inWishlist.includes(i) ? '#EA5455' : '#626262'
                  }
                />
                <span className="align-middle ml-50"> Wishlist</span>
              </div>
              <div className="cart" onClick={() => this.handleAddToCart(i, product.productId)}>
                <ShoppingCart size={15} />
                <span className="align-middle ml-50">
                  {this.state.inCart.includes(i) ? (
                    <Link to="/ecommerce/checkout" className="text-white">
                      {' '}
                      View In Cart{' '}
                    </Link>
                  ) : (
                    'Add to cart'
                  )}
                </span>
              </div>
            </div>
          </div>
        </Card>
      )
    })
    return (
      <div className="shop-content">
        <Row>
          <Col sm="12">
            <div className="ecommerce-header-items">
              <div className="result-toggler w-25 d-flex align-items-center">
                <div className="shop-sidebar-toggler d-block d-lg-none">
                  {/* <Menu
                    size={26}
                    onClick={() => this.props.mainSidebar(true)}
                  />*/}
                </div> 
                <div className="search-results">{this.state.data.length} Results Found</div>
              </div>
              <div className="view-options d-flex justify-content-end w-75">
                <Select
                  className="React-Select"
                  classNamePrefix="select"
                  defaultValue={sortOptions[0]}
                  name="sort"
                  options={sortOptions}
                />
                <div className="view-btn-option">
                  <Button
                    color="white"
                    className={`view-btn ml-1 ${
                      this.state.view === 'grid-view' ? 'active' : ''
                    }`}
                    onClick={() => this.handleView('grid-view')}
                  >
                    <Grid size={24} />
                  </Button>
                  <Button
                    color="white"
                    className={`view-btn ${
                      this.state.view === 'list-view' ? 'active' : ''
                    }`}
                    onClick={() => this.handleView('list-view')}
                  >
                    <List size={24} />
                  </Button>
                </div>
              </div>
            </div>
          </Col>
          <Col sm="12">
            <div className="ecommerce-searchbar mt-1">
              <FormGroup className="position-relative">
                <Input
                  className="search-product"
                  placeholder="Search Here..."
                />
                <div className="form-control-position">
                  <Search size={22} />
                </div>
              </FormGroup>
            </div>
          </Col>
          {window.screen.width < 500  && 
          <Col sm="12">
          <div >
                   <Card>
                          <CardHeader>
                              <CardTitle>< FormattedMessage id="Upload Presciption" /></CardTitle>
                          </CardHeader>
                          <CardBody>
                          <ProgrammaticallyDropzone
                            setfilesOndrop={this.setfilesOndrop}
                            resetDropzone={this.state.reset}
                          />
                {/* <FormGroup className="form-label-group">
                  <Input
                    type='file'
                    //className="datepicker-input"
                    style={{ zIndex: 999 }}
                    name="city"
                    id="cityMulti"
                    placeholder="Upload Presciption"
                    onChange={ev => {
                      this.changeValue(
                        ev.target ? ev.target.value : ev.value,
                     
                      )
                    }}
                  />
                  
                </FormGroup> */}
                </CardBody>
                </Card>
              </div>
              </Col>}
          <Col sm="12">
            <div id="ecommerce-products" className={this.state.view}>
              {renderProducts}
            </div>
          </Col>
          <Col sm="12">
            <div className="ecommerce-pagination">
              <Pagination className="d-flex justify-content-center mt-2">
                <PaginationItem className="prev-item">
                  <PaginationLink href="#" first>
                    <ChevronLeft />
                  </PaginationLink>
                </PaginationItem>
                <PaginationItem active>
                  <PaginationLink href="#">1</PaginationLink>
                </PaginationItem>
                <PaginationItem>
                  <PaginationLink href="#">2</PaginationLink>
                </PaginationItem>
                <PaginationItem>
                  <PaginationLink href="#">3</PaginationLink>
                </PaginationItem>
                <PaginationItem>
                  <PaginationLink href="#">4</PaginationLink>
                </PaginationItem>
                <PaginationItem>
                  <PaginationLink href="#">5</PaginationLink>
                </PaginationItem>
                <PaginationItem>
                  <PaginationLink href="#">6</PaginationLink>
                </PaginationItem>
                <PaginationItem>
                  <PaginationLink href="#">7</PaginationLink>
                </PaginationItem>
                <PaginationItem href="#" className="next-item">
                  <PaginationLink href="#" last>
                    <ChevronRight />
                  </PaginationLink>
                </PaginationItem>
              </Pagination>
            </div>
          </Col>
        </Row>
      </div>
    )
  }
}

